body {
  margin: 0;
  padding: 0;
  font-family: 'Gotham', sans-serif;
  font-size: 1em;
  height: 100vh;
  overflow: hidden;
  background: #ffe600;
}

section {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  min-height: 100vh;
  -webkit-transition: opacity .5s ease, -webkit-transform .5s cubic-bezier(0.4, 0.0, 0.2, 1);
  transition: opacity .5s ease, -webkit-transform .5s cubic-bezier(0.4, 0.0, 0.2, 1);
  -o-transition: transform .5s cubic-bezier(0.4, 0.0, 0.2, 1), opacity .5s ease;
  transition: transform .5s cubic-bezier(0.4, 0.0, 0.2, 1), opacity .5s ease;
  transition: transform .5s cubic-bezier(0.4, 0.0, 0.2, 1), opacity .5s ease, -webkit-transform .5s cubic-bezier(0.4, 0.0, 0.2, 1);
}

.slide-left-enter,
.slide-left-exit-active {
  -webkit-transform: translateX(-100%);
      -ms-transform: translateX(-100%);
          transform: translateX(-100%);
  opacity: 0;
}

.slide-right-enter,
.slide-right-exit-active {
  -webkit-transform: translateX(100%);
      -ms-transform: translateX(100%);
          transform: translateX(100%);
  opacity: 0;
}

.slide-left-enter-active,
.slide-right-enter-active {
  -webkit-transform: translateX(0%);
      -ms-transform: translateX(0%);
          transform: translateX(0%);
  opacity: 1;
}

.slide-left-enter-active,
.slide-right-exit-active {
  -webkit-transition-delay: .5s;
       -o-transition-delay: .5s;
          transition-delay: .5s;
}



